.barraNavegacion{
    z-index: 1;
    position:fixed;
    display:flex;
    justify-content: right;
    background-color: rgba(0,0,0,0.3);
    width:100%;
    .seccion{
        color:rgba(255,255,255,0.3);
        margin:13px;
        border:solid transparent 2px;
        text-decoration: none;
    }

    .seccion:hover{
        cursor:pointer;
        border-bottom:solid white 2px;
        color:rgba(255,255,255,1);
    }
}

.cerrar{
    color:rgba(255,255,255,0.3);
    margin:2rem;
    position:fixed;
    right:10%;
    z-index: 10;
}

.cerrar:hover{
    color:rgba(255,255,255,1);
    cursor: pointer;
}

