$anchoContenedor:45rem;

.contactoContenedor{
    display:flex;
    flex-direction: column;
    color:white;
    text-align: center;
    padding:1rem 0;


    .formularioContenedor{
        display:flex;
        flex-direction: row;
        flex-wrap: wrap;
        max-width: $anchoContenedor;
        justify-content: space-between;
        margin:auto;

        .campoContenedor{
            display:flex;
            flex-direction: column;
            text-align: left;
            margin:0.5rem 0;

            .etiqueta{
                font-size:15px;
                margin:0.5rem 0;
                user-select: none;
            }

            .entrada{
                border-radius: 5px;
                width:20rem;
                height: 3rem;
                font-size: 1.1rem;
                font-family: Helvetica, Arial, sans-serif;
            }

            .mensaje{
                width: $anchoContenedor;
                max-width: $anchoContenedor;
                min-width: $anchoContenedor;
                height: 10rem;

                min-height: 3rem;
                max-height: 10rem;

                overflow-y: scroll;
            }
        }

        .mensajeContenedor{
            display:flex;
            flex-direction: row;
            flex-wrap: wrap;

            .etiqueta{
                width: 50%;
            }

            .caracteres{
                font-size: 0.75rem;
                width: 45%;
                text-align: right;
                margin:auto;
                user-select: none;
            }

        }

        .enviar{
            font-family: 'Inter';
            font-weight: 200;
            color:white;
            background-color: #3C5BC0;
            width: 7rem;
            height: 3rem;
            border-radius: 7px;
            margin: 1rem auto;
            user-select: none;
            border:none;
            
        }

        .enviar:hover{
            background-color: #7696ff;
            cursor: pointer;
        }

    }
}