.carruselContenedor{
    margin:auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content:center;
    user-select: none;
    
    .flechaCarrusel{
        padding:25px;
        opacity:30%;
    }


    .descripcion{
        padding-top:20px;
        margin:auto;
        width:75%;
    }

    .flechaCarrusel:hover{
        opacity:100%;
        cursor:pointer;
    }
}

.tipoProyecto{
    font-size: 1rem;
    font-weight: 200;
    user-select: none;
}