$colorTarjeta: #353535;
$tamanoMaxTarjeta:15rem;

.habilidadesContenedor{
    color:white;
    padding:1rem 0;
    text-align: center;
    justify-items: center;

    .seccionesContenedor{
        display:flex;
        flex-direction: row;
        flex-wrap: wrap;

        .habilidad{
            font-size:1rem;
            font-weight: 200;
            float:left;
        }

        .tecnicas, .blandas {
            display:flex;
            flex-direction: column;
        }


        .tarjetasContenedor{
            display:flex;
            flex-direction: row;
            margin: 0 1rem;

            .tarjeta{
                display:flex;
                flex-direction: column;
                flex-wrap: wrap;
                max-height: $tamanoMaxTarjeta;
                background-color:$colorTarjeta;
                border-radius:1rem;
                padding:0.8rem;
                margin:1rem;
            
                .tituloContenedor{
                    writing-mode:vertical-rl;
                    -webkit-transform: rotate(180deg);
                    justify-items:center;
                    height: 100%;
                    .titulo{
                        font-size: 1.6rem;
                        height: 100%;
                    }
                }

                .areas{
                    display:flex;
                    flex-direction: column;
                    flex-wrap:wrap;
                    max-height: $tamanoMaxTarjeta;
                    margin:0.5rem;
                    .area{
                        font-size: 12px;
                        text-align: right;
                        margin:0.2rem 0.5rem;
                        width:8rem;
                        max-width: 8rem;
                    }
                .tecnologias{
                    display:flex;
                    flex-direction: row;
                        .iconosContenedor{
                            display:flex;
                            flex-direction:column;
                            width: 2rem;
                            margin-right:0.3rem;
                            align-items: center;

                            .icono{
                                width: 1rem;
                                height:1rem;
                                margin:0.5rem;

                            }

                            .css{
                                width:0.75rem;
                            }

                            .pequeno{
                                margin-top:1rem;
                                height:7px;
                                width: 1.5rem;
                                padding-right:15px;
                            }

                            .grande{
                                height:1rem;
                                width: 1.5rem;
                                margin: 0.5rem 0;
                            }
                            
                        }

                        .textoContenedor{
                            display:flex;
                            flex-direction:column;
                            .texto{
                                text-align: left;
                                margin:0.5rem 0;
                                height: 1rem;
                            }
                        }

                        
                    }
                }
            }

            .tarjeta-grande{
                display:flex;
                flex-direction: column;
                background-color:$colorTarjeta;
                border-radius:1rem;
                padding:0.8rem;
                margin:1rem;

                .areas{
                    display:flex;
                    flex-direction: column;
                    justify-content: right;
                    
                    .area{
                        font-size: 12px;
                        text-align: right;
                        margin:0.2rem 0.5rem;
                    }
                .tecnologias{
                    display:flex;
                    flex-direction: row;
                        .textoContenedor{
                            display:flex;
                            flex-direction:column;
                            .texto{
                                text-align: left;
                                margin:0.5rem 0;
                            }
                        }

                        
                    }
                }
            }

        }
    }
}

