$tamanoBorde:3px;

.educacionContenedor{
    display:flex;
    flex-direction: column;
    color:white;
    padding:1rem 0;
    text-align: center;
    justify-items: center;

    .certificadosContenedor{
        display:flex;
        flex-wrap:wrap;
        margin:0 10%;
        justify-content:space-evenly;

        .certificado{
            width:13.33rem;
            margin: 0.66rem;
            border: transparent solid $tamanoBorde;
        }

        .certificado:hover{
            cursor: pointer;
            border: rgb(0, 225, 255) solid  $tamanoBorde;
        }
    }
}

.certificadoCompletoContenedor{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(27, 27, 27, 0.8);
    backdrop-filter: blur(2px);
    display: flex;
    align-items: center;
    justify-content:center;
    z-index: 2;
    user-select:none;

    .cerrar{
        position:absolute;
        top:7%;
        right:20%;
        color: rgba(255,255,255,0.3);
    }

    .cerrar:hover{
        color: rgba(255,255,255, 1);
        cursor: pointer;
    }
}
