$alturaEncabezado:360px;

.encabezado{
    width: 100%;
    height: $alturaEncabezado;
    display:flex;
    flex-direction: column;
    position:relative;
    align-items: center;
    justify-content: center;

    .imagenEncabezado{
        position:absolute;
        z-index: -10;
        object-fit:cover;
        width: 100%;
        height: $alturaEncabezado;
    }
    .nombreContenedor{
        text-align: center;
        padding:1rem;
        color:white;
        display:flex;
        flex-direction: column;
        border:solid white 2px;
        
        .nombre{
            margin:0;
            padding:0;
            font-size: 2.33rem;
        }

        .titulo{
            margin:0;
            padding:0;
            font-size:1.33rem;
        }
    }

    .contenedorImagen{
        display:flex;
        justify-items: center;
        position:absolute;
        margin-top: $alturaEncabezado;
        .imagenPerfil{
            width:10rem;
            border-radius: 10rem;
            border: white solid 2px;
        }

    }
}