.barraNavegacion {
  z-index: 1;
  position: fixed;
  display: flex;
  justify-content: right;
  background-color: rgba(0, 0, 0, 0.3);
  width: 100%;
}
.barraNavegacion .seccion {
  color: rgba(255, 255, 255, 0.3);
  margin: 13px;
  border: solid transparent 2px;
  text-decoration: none;
}
.barraNavegacion .seccion:hover {
  cursor: pointer;
  border-bottom: solid white 2px;
  color: rgb(255, 255, 255);
}

.cerrar {
  color: rgba(255, 255, 255, 0.3);
  margin: 2rem;
  position: fixed;
  right: 10%;
  z-index: 10;
}

.cerrar:hover {
  color: rgb(255, 255, 255);
  cursor: pointer;
}

.encabezado {
  width: 100%;
  height: 360px;
  display: flex;
  flex-direction: column;
  position: relative;
  align-items: center;
  justify-content: center;
}
.encabezado .imagenEncabezado {
  position: absolute;
  z-index: -10;
  object-fit: cover;
  width: 100%;
  height: 360px;
}
.encabezado .nombreContenedor {
  text-align: center;
  padding: 1rem;
  color: white;
  display: flex;
  flex-direction: column;
  border: solid white 2px;
}
.encabezado .nombreContenedor .nombre {
  margin: 0;
  padding: 0;
  font-size: 2.33rem;
}
.encabezado .nombreContenedor .titulo {
  margin: 0;
  padding: 0;
  font-size: 1.33rem;
}
.encabezado .contenedorImagen {
  display: flex;
  justify-items: center;
  position: absolute;
  margin-top: 360px;
}
.encabezado .contenedorImagen .imagenPerfil {
  width: 10rem;
  border-radius: 10rem;
  border: white solid 2px;
}

.acercaDeContenedor {
  display: flex;
  flex-direction: column;
  color: white;
  padding: 100px 0 1rem 0;
  text-align: center;
}
.acercaDeContenedor .descripcion {
  margin: auto;
  width: 75%;
}

.proyectosContenedor {
  display: flex;
  flex-direction: column;
  color: white;
  padding: 1rem 0;
  text-align: center;
}
.proyectosContenedor .subtitulo {
  font-weight: 200;
  font-size: 2rem;
}
.proyectosContenedor .descripcion {
  padding: 2.33rem;
  margin: auto;
  width: 75%;
}

.carruselContenedor {
  margin: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  user-select: none;
}
.carruselContenedor .flechaCarrusel {
  padding: 25px;
  opacity: 30%;
}
.carruselContenedor .descripcion {
  padding-top: 20px;
  margin: auto;
  width: 75%;
}
.carruselContenedor .flechaCarrusel:hover {
  opacity: 100%;
  cursor: pointer;
}

.tipoProyecto {
  font-size: 1rem;
  font-weight: 200;
  user-select: none;
}

.educacionContenedor {
  display: flex;
  flex-direction: column;
  color: white;
  padding: 1rem 0;
  text-align: center;
  justify-items: center;
}
.educacionContenedor .certificadosContenedor {
  display: flex;
  flex-wrap: wrap;
  margin: 0 10%;
  justify-content: space-evenly;
}
.educacionContenedor .certificadosContenedor .certificado {
  width: 13.33rem;
  margin: 0.66rem;
  border: transparent solid 3px;
}
.educacionContenedor .certificadosContenedor .certificado:hover {
  cursor: pointer;
  border: rgb(0, 225, 255) solid 3px;
}

.certificadoCompletoContenedor {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(27, 27, 27, 0.8);
  backdrop-filter: blur(2px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
  user-select: none;
}
.certificadoCompletoContenedor .cerrar {
  position: absolute;
  top: 7%;
  right: 20%;
  color: rgba(255, 255, 255, 0.3);
}
.certificadoCompletoContenedor .cerrar:hover {
  color: rgb(255, 255, 255);
  cursor: pointer;
}

.habilidadesContenedor {
  color: white;
  padding: 1rem 0;
  text-align: center;
  justify-items: center;
}
.habilidadesContenedor .seccionesContenedor {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.habilidadesContenedor .seccionesContenedor .habilidad {
  font-size: 1rem;
  font-weight: 200;
  float: left;
}
.habilidadesContenedor .seccionesContenedor .tecnicas, .habilidadesContenedor .seccionesContenedor .blandas {
  display: flex;
  flex-direction: column;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor {
  display: flex;
  flex-direction: row;
  margin: 0 1rem;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor .tarjeta {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-height: 15rem;
  background-color: #353535;
  border-radius: 1rem;
  padding: 0.8rem;
  margin: 1rem;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor .tarjeta .tituloContenedor {
  writing-mode: vertical-rl;
  -webkit-transform: rotate(180deg);
  justify-items: center;
  height: 100%;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor .tarjeta .tituloContenedor .titulo {
  font-size: 1.6rem;
  height: 100%;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor .tarjeta .areas {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-height: 15rem;
  margin: 0.5rem;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor .tarjeta .areas .area {
  font-size: 12px;
  text-align: right;
  margin: 0.2rem 0.5rem;
  width: 8rem;
  max-width: 8rem;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor .tarjeta .areas .tecnologias {
  display: flex;
  flex-direction: row;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor .tarjeta .areas .tecnologias .iconosContenedor {
  display: flex;
  flex-direction: column;
  width: 2rem;
  margin-right: 0.3rem;
  align-items: center;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor .tarjeta .areas .tecnologias .iconosContenedor .icono {
  width: 1rem;
  height: 1rem;
  margin: 0.5rem;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor .tarjeta .areas .tecnologias .iconosContenedor .css {
  width: 0.75rem;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor .tarjeta .areas .tecnologias .iconosContenedor .pequeno {
  margin-top: 1rem;
  height: 7px;
  width: 1.5rem;
  padding-right: 15px;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor .tarjeta .areas .tecnologias .iconosContenedor .grande {
  height: 1rem;
  width: 1.5rem;
  margin: 0.5rem 0;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor .tarjeta .areas .tecnologias .textoContenedor {
  display: flex;
  flex-direction: column;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor .tarjeta .areas .tecnologias .textoContenedor .texto {
  text-align: left;
  margin: 0.5rem 0;
  height: 1rem;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor .tarjeta-grande {
  display: flex;
  flex-direction: column;
  background-color: #353535;
  border-radius: 1rem;
  padding: 0.8rem;
  margin: 1rem;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor .tarjeta-grande .areas {
  display: flex;
  flex-direction: column;
  justify-content: right;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor .tarjeta-grande .areas .area {
  font-size: 12px;
  text-align: right;
  margin: 0.2rem 0.5rem;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor .tarjeta-grande .areas .tecnologias {
  display: flex;
  flex-direction: row;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor .tarjeta-grande .areas .tecnologias .textoContenedor {
  display: flex;
  flex-direction: column;
}
.habilidadesContenedor .seccionesContenedor .tarjetasContenedor .tarjeta-grande .areas .tecnologias .textoContenedor .texto {
  text-align: left;
  margin: 0.5rem 0;
}

.contactoContenedor {
  display: flex;
  flex-direction: column;
  color: white;
  text-align: center;
  padding: 1rem 0;
}
.contactoContenedor .formularioContenedor {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 45rem;
  justify-content: space-between;
  margin: auto;
}
.contactoContenedor .formularioContenedor .campoContenedor {
  display: flex;
  flex-direction: column;
  text-align: left;
  margin: 0.5rem 0;
}
.contactoContenedor .formularioContenedor .campoContenedor .etiqueta {
  font-size: 15px;
  margin: 0.5rem 0;
  user-select: none;
}
.contactoContenedor .formularioContenedor .campoContenedor .entrada {
  border-radius: 5px;
  width: 20rem;
  height: 3rem;
  font-size: 1.1rem;
  font-family: Helvetica, Arial, sans-serif;
}
.contactoContenedor .formularioContenedor .campoContenedor .mensaje {
  width: 45rem;
  max-width: 45rem;
  min-width: 45rem;
  height: 10rem;
  min-height: 3rem;
  max-height: 10rem;
  overflow-y: scroll;
}
.contactoContenedor .formularioContenedor .mensajeContenedor {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.contactoContenedor .formularioContenedor .mensajeContenedor .etiqueta {
  width: 50%;
}
.contactoContenedor .formularioContenedor .mensajeContenedor .caracteres {
  font-size: 0.75rem;
  width: 45%;
  text-align: right;
  margin: auto;
  user-select: none;
}
.contactoContenedor .formularioContenedor .enviar {
  font-family: "Inter";
  font-weight: 200;
  color: white;
  background-color: #3C5BC0;
  width: 7rem;
  height: 3rem;
  border-radius: 7px;
  margin: 1rem auto;
  user-select: none;
  border: none;
}
.contactoContenedor .formularioContenedor .enviar:hover {
  background-color: #7696ff;
  cursor: pointer;
}

footer .redesContenedor {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-top: 1rem;
}
footer .redesContenedor .contenedorLogo {
  width: 3rem;
  margin: 0 5rem 1rem 5rem;
}
footer .redesContenedor .contenedorLogo .logoRedes:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.creditosContenedor {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(27, 27, 27, 0.8);
  backdrop-filter: blur(2px);
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 2;
  user-select: none;
  color: white;
  overflow-y: scroll;
  scrollbar-width: thin;
}
.creditosContenedor .titulo {
  font-weight: 200;
  font-size: 2rem;
}
.creditosContenedor .credito {
  width: 60%;
  display: flex;
  flex-direction: column;
  text-align: left;
  align-items: left;
  margin: 1rem;
}
.creditosContenedor .credito .link {
  text-decoration: none;
  color: yellow;
}

body {
  background: #353535;
  font-size: 15px;
}

.subtitulo {
  font-weight: 200;
  font-size: 2rem;
}

.apartado {
  background-color: #484848;
  margin-top: 2rem;
}

.sombra {
  filter: drop-shadow(6px 6px 2px #2b2b2b);
}

