.creditosContenedor{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(27, 27, 27, 0.8);
    backdrop-filter: blur(2px);
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 2;
    user-select:none;
    color:white;
    overflow-y:scroll;
    scrollbar-width: thin;

    .titulo{
        font-weight: 200;
        font-size: 2rem;
    }

    .credito{
        width: 60%;
        display:flex;
        flex-direction: column;
        text-align: left;
        align-items: left;
        margin:1rem;

        .link{
            text-decoration: none;
            color:yellow;
        }
    }
}